import Color from '/web_modules/color.js';
import App from './lib/app.js';
import { getRandomHex } from './lib/utils/index.js';
var data = {
  color: document.location.hash
};

try {
  Color(data.color);
} catch (err) {
  data.color = getRandomHex();
}

var app = new App(data);
app.run();
window.app = app;